<script>
    import InstaController from './Controller';
    import InstaPost from './InstaPost';
    import Loading from './components/Loading';

    export default {
        name: 'InstaScroll',
        inheritAttrs: false,
        components: {InstaController, InstaPost, Loading},
        props: {
            speed: {type: [Number, String], default: 1.5},
            pauseOnHover: Boolean,
            gap: {type: [String, Number], default: '0'},
            size: {type: [String, Number], default: 320},
        },
        data() {
            return {
                isLoaded: false,
                isPaused: 0,
                offset: 0,
                items: [],
            };
        },
        computed: {
            visible() {
                return [...this.items, ...this.items];
            },
            itemStyles() {
                return {
                    height: `${this.size}px`,
                    width: `${this.size}px`,
                    marginRight: `${this.gap}px`,
                }
            }
        },
        methods: {
            move() {
                this.offset -= parseFloat(this.speed);
                const firstChild = this.$refs.scroller.$el.querySelector('.insta-post');
                const scrollerBox = this.$el.getBoundingClientRect();
                if (firstChild) {
                    const box = firstChild.getBoundingClientRect();
                    const breakpoint = box.x + box.width + parseInt(this.gap);
                    if (breakpoint <= scrollerBox.left) {
                        this.$refs.scroller.$el.style.transform = `translateX(0)`;
                        this.items.push(this.items.shift());
                        this.offset = 0;
                    }
                }
                if (!this.isPaused) {
                    requestAnimationFrame(this.move.bind(this));
                }
                this.$refs.scroller.$el.style.transform = `translateX(${this.offset}px)`;
            },
            onLoaded(items) {
                this.isLoaded = true;
                this.items = items;
                this.$nextTick(this.start);
            },
            onHover() {
                if (this.pauseOnHover) {
                    this.pause();
                }
            },
            start() {
                this.$nextTick(() => {
                    requestAnimationFrame(this.move.bind(this));
                });
            },
            pause() {
                this.isPaused = true;
            },
            resume() {
                if (this.isPaused) {
                    this.isPaused = false;
                    requestAnimationFrame(this.move.bind(this));
                }
            }
        },
    };
</script>

<template>
    <div class="insta-scroll" ref="scroll" @mouseenter="onHover" @mouseleave="resume">
        <insta-controller v-bind="$attrs" @load="onLoaded" class="insta-scroll-inner" ref="scroller">
            <loading :active="!isLoaded"></loading>
            <insta-post
                    v-bind="$attrs"
                    :item="item"
                    v-for="(item, index) in visible"
                    :key="`item.id-${index}`"
                    :style="itemStyles">

            </insta-post>
        </insta-controller>
    </div>
</template>

<style lang="scss" scoped>
    .insta-scroll {
        overflow: hidden;
        &-inner {
            position: relative;
            display: flex;
            > div {
                flex-shrink: 0;
                padding-bottom: 0;
            }
        }
    }
</style>