import changeCase from 'change-case';

export function snippet(tag, data) {
    let attrs = [];
    Object
        .entries(data)
        .filter(([_, value]) => !!value)
        .forEach(([name, value]) => {
            name = changeCase.paramCase(name);
            if (typeof(value) === 'boolean') {
                attrs.push(name);
            } else {
                attrs.push(`${name}="${value}"`);
            }
        });
    return `<${tag} ${attrs.join(' ')}></${tag}>`;
}