<script>
    import Icons from './components/Icons';
    import Author from './components/Author';

    export default {
        name: 'InstaOverlaySimple',
        props: {
            item: {type: Object, required: true},
            bg: {type: String, default: 'rgba(0, 0, 0, .7)'},
            textColor: {type: String, default: '#fff'},
            avatarSize: {type: String, default: '100px'},
            showAuthor: Boolean,
        },
        components: {Icons, Author},
        computed: {
            overlayStyles() {
                return {
                    color: this.textColor,
                    stroke: this.textColor,
                    fill: this.textColor,
                    backgroundColor: this.bg,
                };
            }
        },
    };
</script>

<template>
    <div class="overlay" :style="overlayStyles">
        <author :post="item.post" v-if="item.post && showAuthor" :size="avatarSize"></author>
        <icons :item="item"></icons>
    </div>
</template>

<style lang="scss" scoped>
    .overlay {
        position: absolute;
        top: 0;
        right: 0;
        left: 0;
        bottom: 0;
        display: none;
        flex-direction: column;
        transition: .7s;
        justify-content: center;
        background: transparent;

        .icons {
            margin-top: 32px;
        }

        @media (max-width: 800px) {
            .icons {
                margin-top: 0;
            }
        }
    }
</style>