<script>
    import Vue from 'vue';
    import api from './api';

    export default {
        name: 'InstaController',
        props: {
            hashtag: String,
            username: String,
            location: String,
            count: [String, Number],
            shuffle: Boolean
        },
        data() {
            return {
                items: [],
                total: '',
                loading: true,
                initialLoad: true,
            };
        },
        created() {
            if (!this.hashtag && !this.username && !this.location) {
                throw new Error('Instagram plugin requires `hashtag` or `username` or `location` attribute set.');
            }
            this.search({
                tags: this.hashtag,
                users: this.username,
                locations: this.location,
            });
        },
        computed: {
            visible() {
                if (!this.count) {
                    return this.items;
                }

                return this.items.slice(0, this.count);
            }
        },
        methods: {
            async search(criteria) {
                this.loading = true;
                try {
                    const response = await api.search(criteria);
                    this.total = response.count;
                    this.items = response.items;

                    if (this.shuffle) {
                        this.items = this.doShuffle(this.items);
                    }

                    this.$emit('load', this.visible);
                    if (this.initialLoad) {
                        this.$emit('initial-load', this.visible);

                    }
                } catch (e) {
                    console.error(e);
                    this.$emit('error');
                } finally {
                    this.loading = false;
                    this.initialLoad = false;
                }
            },
            async prefetchPosts(images) {
                images.forEach(item => {
                    api.loadPost(item.shortcode).then(post => {
                        Vue.set(item, 'post', post);
                    });
                });
            },
            doShuffle(array) {
                var currentIndex = array.length, temporaryValue, randomIndex;
                while (0 !== currentIndex) {
                    randomIndex = Math.floor(Math.random() * currentIndex);
                    currentIndex -= 1;
                    temporaryValue = array[currentIndex];
                    array[currentIndex] = array[randomIndex];
                    array[randomIndex] = temporaryValue;
                }
                return array;
            },
        },
        watch: {
            hashtag(val) {
                this.search({
                    tags: val,
                    users: this.username,
                    locations: this.location,
                });
            },
            username(val) {
                this.search({
                    tags: this.hashtag,
                    users: val,
                    locations: this.location,
                });
            },
            location(val) {
                this.search({
                    tags: this.hashtag,
                    users: this.username,
                    locations: val,
                });
            },
        }
    };
</script>

<template>
    <div v-bind="$attrs">
        <slot :items="visible" :total="total" :loading="loading" :initial-load="initialLoad"></slot>
        <!--<div v-else>-->
            <!--loading...-->
        <!--</div>-->
    </div>
</template>
