<script>
    export default {
        name: 'Author',
        props: {
            post: {type: Object, required: true},
        },
        computed: {
            picStyles() {
                new Image().src = this.post.owner.profile_pic_url;

                let bg = 'transparent';
                bg = `url(${this.post.owner.profile_pic_url})`;
                return {
                    backgroundImage: bg,
                };
            },
            owner() {
                return this.post.owner;
            },
        }
    };
</script>

<template>
    <div class="insta-author">
        <div class="insta-author-userpic" :style="picStyles"></div>
        <div>
            {{ owner.full_name }}
        </div>
    </div>
</template>

<style lang="scss" scoped>
    .insta-author {
        text-align: center;
        font-size: 20px;

        &-userpic {
            border-radius: 100%;
            height: 100px;
            width: 100px;
            background-position: center center;
            margin: 0 auto 16px;

        }

        @media (max-width: 1000px) {
            &-userpic {
                height: 50px;
                width: 50px;
            }
