<script>
    import InstaController from './Controller';
    import InstaPost from './InstaPost';
    import Loading from './components/Loading';

    export default {
        name: 'InstaGrid',
        components: {Loading, InstaController, InstaPost},
        props: {
            columns: {type: [String, Number], default: 6},
            rows: {type: [String, Number], default: 2},
            gap: {type: [String, Number], default: 0},
        },
        computed: {
            styles() {
                const cols = [];
                for (let i = 0; i < this.columns; i++) {
                    cols.push('1fr');
                }

                const rows = [];
                for (let i = 0; i < this.rows; i++) {
                    rows.push('1fr');
                }

                return {
                    'grid-gap': `${this.gap}px`,
                    'grid-template-columns': cols.join(' '),
                    '-ms-grid-columns': cols.join(' '),
                    'grid-template-rows': rows.join(' '),
                    '-ms-grid-rows': rows.join(' '),
                };
            },
            maxItems() {
                return this.columns * this.rows;
            }
        }
    };
</script>

<template>
    <insta-controller v-bind="$attrs" :count="maxItems">
        <div class="insta-grid" :style="styles" slot-scope="{items, loading}">
            <loading :active="loading"></loading>
            <insta-post v-bind="$attrs" :item="item" v-for="item in items" :key="item.id">
                <!--<template slot="overlay">-->
                <!--<slot name="overlay" :item="item"></slot>-->
                <!--</template>-->
            </insta-post>
        </div>
    </insta-controller>
</template>

<style lang="scss" scoped>
    .insta-grid {
        display: -ms-grid;
        display: grid;

        .insta-post {
            padding-bottom: 0;
            .image {
                padding: 50% 0;
            }
        }
    }
</style>