<script>
    import InstaController from './Controller';
    import InstaPost from './InstaPost';
    import Loading from './components/Loading';

    export default {
        name: 'InstaSlider',
        inheritAttrs: false,
        components: {Loading, InstaController, InstaPost},
        props: {
            axis: {type: String, default: 'horizontal'},
            slideBy: {type: [Number, String], default: 'page'},
            gap: {type: [String, Number], default: '0'},
            count: {type: [String, Number], default: 6},
        },
        data() {
            return {
                page: 0,
                isLoaded: false,
                items: [],
            };
        },
        computed: {
            pages() {
                return this.items.length / parseInt(this.count);
            },
            offset() {
                return (this.page * 100);
            },
            itemStyles() {
                const width = (100 / parseInt(this.count)).toFixed(2);
                return {
                    width: `calc(${width}% - ${this.gap}px)`,
                    marginRight: `calc(${this.gap / 2}px)`,
                    marginLeft: `calc(${this.gap / 2}px)`,
                };
            },
            styles() {
                return {
                    transform: `translateX(-${this.offset}%)`
                };
            },
            hasNext() {
                return ((this.page + 1) * parseInt(this.count)) < this.items.length;
            }
        },
        methods: {
            onLoaded(items) {
                this.items = items;
                this.isLoaded = true;
            },
            next() {
                this.page += 1;
            },
            prev() {
                this.page -= 1;
            },
            isActive(index) {
                const count = parseInt(this.count);
                const start = this.page * count;
                const end = start + count;
                return index >= start && index < end;
            }
        }
    };
</script>

<template>
    <div class="insta-slider" ref="scroll">
        <button class="btn btn-prev" @click.prevent="prev" v-if="isLoaded && page > 0">
            <svg viewBox="0 0 50 80" xml:space="preserve">
                <polyline fill="none"
                          stroke-width="5"
                          stroke-linecap="round" stroke-linejoin="round"
                          points="45.63,75.8 0.375,38.087 45.63,0.375 "/>
            </svg>
        </button>
        <insta-controller v-bind="$attrs" @load="onLoaded" class="insta-slider-inner" ref="slider" :style="styles">
            <loading :active="!isLoaded"></loading>
            <insta-post
                    v-bind="$attrs"
                    v-for="(item, index) in items"
                    :class="{active: isActive(index)}"
                    :item="item"
                    :key="`item.id-${index}`"
                    :style="itemStyles">

            </insta-post>
        </insta-controller>
        <button class="btn btn-next" @click.prevent="next" v-if="isLoaded && hasNext">
            <svg xmlns="http://www.w3.org/2000/svg"  width="60px" height="80px" viewBox="0 0 50 80" xml:space="preserve">
                <polyline fill="none"
                          stroke-width="5"
                          stroke-linecap="round"
                          stroke-linejoin="round" points="0.375,0.375 45.63,38.087 0.375,75.8 "/>
            </svg>
        </button>

    </div>
</template>

<style lang="scss" scoped>
