        position: relative;

        .btn {
            position: absolute;
            z-index: 2000;
            top: 50%;
            font-size: 0;
            line-height: 0;
            display: block;
            -webkit-transform: translate(0, -50%);
            -ms-transform: translate(0, -50%);
            transform: translate(0, -50%);
            cursor: pointer;
            color: transparent;
            border: none;
            outline: none;
            background: rgba(0, 0, 0, .5);
            border-radius: 100%;
            padding: 8px;
            margin: 0 8px;
            stroke: white;
            transition: .3s;
            opacity: 0;

            svg {
                width: 20px;
                height: 20px;
            }
        }

        .btn-next {
            right: 0;
        }

        .btn-prev {
            left: 0;
        }

        &:hover {
            .btn {
                opacity: 1;
            }
        }
    }
</style>