<script>
    import {snippet} from './utils';

    export default {
        name: 'Snippet',
        props: ['attrs', 'tag'],
        computed: {
            html() {
                return snippet(this.tag, this.attrs);
            }
        }
    };
</script>

<template>
    <div class="card">
        <div class="card-body">
            <div class="card-title">Copy this snippet</div>
            <pre>{{ html }}</pre>
        </div>
    </div>
</template>
