import Vue from 'vue';
import './assets/tabler.css';
import App from './demo/App.vue';
import router from './demo/router';
import VueAnalytics from 'vue-analytics';

Vue.use(VueAnalytics, {
    router,
    id: 'UA-122324216-4',
    checkDuplicatedScript: true,
    autoTracking: {
        skipSamePath: true
    },
});

Vue.config.productionTip = false;
new Vue({
    router,
    render: h => h(App)
}).$mount('#app');
