import 'whatwg-fetch';

export default {
    async fetch(url, responseType = 'json') {
        let proxy = window.instafeedProxyUrl || null;
        if (proxy) {
            url = proxy + '?' + url;
        }
        const response = await window.fetch(url, {
            cors: 'no-cors',
            referrer: 'no-referer',
        });
        return {
            statusCode: response.status,
            data: await (responseType == 'json'
                ? response.json()
                : response.text()),
        };
    },

    async search({ tags = '', users = '', locations = '' }) {
        const tasks = [];
        tags.split(',')
            .map(s => s.trim())
            .filter(s => !!s)
            .forEach(val => tasks.push(this.fetchByTag(val)));
        users
            .split(',')
            .map(s => s.trim())
            .filter(s => !!s)
            .forEach(val => tasks.push(this.fetchByUser(val)));
        locations
            .split(',')
            .map(s => s.trim())
            .filter(s => !!s)
            .forEach(val => tasks.push(this.fetchByLocation(val)));

        const result = {
            count: 0,
            items: [],
        };
        const responses = await Promise.all(tasks);
        responses.forEach(response => {
            result.count += response.count;
            result.items = result.items.concat(response.items);
        });
        return result;
    },
    async fetchByTag(tag) {
        const response = await this.fetch(
            `https://www.instagram.com/explore/tags/${tag}/?__a=1`,
        ).then(r => r.data);
        return {
            type: 'tag',
            query: tag,
            count: response.graphql.hashtag.edge_hashtag_to_media.count,
            items: response.graphql.hashtag.edge_hashtag_to_media.edges.map(
                ({ node }) => this.handleNode(node),
            ),
        };
    },
    async fetchByLocation(id) {
        const response = await this.fetch(
            `https://www.instagram.com/explore/locations/${id}/?__a=1`,
        ).then(r => r.data);
        return {
            type: 'location',
            query: id,
            count: response.graphql.location.edge_location_to_media.count,
            items: response.graphql.location.edge_location_to_media.edges.map(
                ({ node }) => this.handleNode(node),
            ),
        };
    },
    async fetchByUser(username, count=48) {
        const profile = await this.fetchProfile(username);
        const response = await this.fetch(
            `https://www.instagram.com/graphql/query/?query_hash=15bf78a4ad24e33cbd838fdb31353ac1&variables={%22id%22:%22${profile.id}%22,%22first%22:${count},%22after%22:%22%22}`
        )
        return {
            type: 'user',
            query: username,
            count: response.data.data.user.edge_owner_to_timeline_media.edges.length,
            items: response.data.data.user.edge_owner_to_timeline_media.edges.map(({ node }) =>
                this.handleNode(node),
            ),
        };
    },
    async loadPost(code) {
        const response = await this.fetch(
            `https://www.instagram.com/p/${code}/?__a=1`,
        ).then(r => r.data);
        const data = response.graphql.shortcode_media;

        let taggedUsers = [];
        if ('edge_media_to_tagged_user' in data) {
            taggedUsers = data.edge_media_to_tagged_user.edges.map(
                ({ node }) => {
                    return {
                        id: node.user.id,
                        username: node.user.username,
                        x: node.x,
                        y: node.y,
                    };
                },
            );
        }
        let images = [];
        if ('edge_sidecar_to_children' in data) {
            images = data.edge_sidecar_to_children.edges.map(({ node }) => {
                return {
                    id: node.id,
                    shortcode: node.shortcode,
                    dimensions: node.dimensions,
                    display_url: node.display_url,
                    is_video: node.is_video,
                    media_preview: node.media_preview,
                };
            });
        }

        return {
            id: data.id,
            shortcode: data.shortcode,
            display_url: data.display_url,
            is_ad: data.is_ad,
            is_video: data.is_video,
            media_preview: data.media_preview,
            caption: data.edge_media_to_caption.edges.map(({ node }) => {
                return node.text;
            }),
            taggedUsers: taggedUsers,
            likes: data.edge_media_preview_like.count,
            comment_count: data.edge_media_to_comment.count,
            comments: data.edge_media_to_comment.edges.map(({ node }) => {
                return {
                    id: node.id,
                    created_at: node.created_at,
                    owner: node.owner,
                    text: node.text,
                };
            }),
            images: images,
            taken_at_timestamp: data.taken_at_timestamp,
            location: data.location,
            owner: data.owner,
            dimensions: data.dimensions,
            display_resources: data.display_resources.reduce((map, obj) => {
                map[obj.config_width] = obj;
                return map;
            }),
        };
    },
    handleNode(node) {
        return {
            id: node.id,
            likes: node.edge_liked_by ? node.edge_liked_by.count : 0,
            owner_id: node.owner.id,
            dimensions: node.dimensions,
            comments: node.edge_media_to_comment.count,
            caption: node.edge_media_to_caption ? node.edge_media_to_caption.edges.map(obj => {
                return obj.node.text;
            }) : '',
            shortcode: node.shortcode,
            video_view_count: node.video_view_count,
            taken_at_timestamp: node.taken_at_timestamp,
            display_url: node.display_url,
            thumbnails: node.thumbnail_resources.reduce((map, obj) => {
                map[obj.config_width] = obj;
                return map;
            }),
            thumbnail_src: node.thumbnail_src,
        };
    },
    async fetchProfile(username) {
        const script = document.createElement('script');
        script.src = `https://www.instagram.com/${username}/?__a=1`;
        script.onload = function() {
            console.log(this);
            console.log(this.innerText);
            console.log(this.innerHTML);
        };
        const response = await this.fetch(
            `https://www.instagram.com/${username}/`,
            'text',
        );
        const jsonString = response.data.match(
            /window._sharedData = (.*);<\/script>/,
        )[1];
        const json = JSON.parse(jsonString);
        return json.entry_data.ProfilePage[0].graphql.user;
    },
};
