<script>
    export default {
        name: 'InstaPost',
        inheritAttrs: false,
        props: {
            hoverEffect: String,
            item: {type: Object, required: true},
            clickable: Boolean,
        },
        created() {
            new Image().src = this.item.thumbnails[480].src;
        },
        computed: {
            classes() {
                const list = [];
                if (this.hoverEffect) {
                    list.push(`effect-${this.hoverEffect}`);
                }

                if (this.clickable) {
                    list.push('clickable');
                }
                return list;
            },
            styles() {
                return {
                    backgroundImage: `url(${this.item.thumbnails[480].src})`
                };
            }
        },
        methods: {
            onClicked() {
                if (this.clickable) {
                    window.open(`https://www.instagram.com/p/${this.item.shortcode}`);
                }
            }
        }
    };
</script>

<template>
    <div class="insta-post" :class="classes" v-bind="$attrs" @click="onClicked">
        <div class="image" :style="styles"></div>
        <!--<slot :item="item">-->
        <!--&lt;!&ndash;<img :src="item.thumbnails[480].src"/>&ndash;&gt;-->
        <!--</slot>-->
        <slot name="overlay" :item="item"></slot>
    </div>
</template>

<style lang="scss" scoped>
    .insta-post {
        position: relative;
        font-size: 1.6vh;
        overflow: hidden;
        width: 100%;
        padding-bottom: 100%;

        .image {
            background-position: center;
            background-repeat: no-repeat;
            background-size: cover;
            left: 0;
            top: 0;
            bottom: 0;
            right: 0;
            padding: 50% 0;
            &:after {
                content: ' ';
            }
        }

        &.clickable {
            cursor: pointer;
        }

        img {
            display: block;
            max-width: 100%;
        }

        &:hover {
            .overlay {
                background: rgba(0, 0, 0, .7);
                display: flex;
            }
        }

        &[rounded] {
            border-radius: 12px;
        }

        &.effect-scale {
            overflow: hidden;
            &:hover {
                > .image {
                    transition: .7s;
                    transform: scale(1.2);
                }
            }
        }

        &.effect-rotate {
            overflow: hidden;

            &:hover {
                > .image {
                    transition: .7s;
                    transform: scale3d(1.1, 1.1, 1) rotateZ(3deg);
                }
            }
        }

        &.effect-opacity {
            overflow: hidden;

            &:hover {
                > .image {
                    transition: .7s;
                    opacity: .7;
                }
            }
        }
    }
</style>